<template>
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
    ref="editModal"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content cartBG">
        <div class="modal-header">
          <h1 class="modal-title fs-5">{{ tempProduct.title }}</h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
            @click="$emit('openCart')"
          ></button>
        </div>
        <div class="modal-body row">
          <div class="col-6">
            <img :src="tempProduct.imageUrl" alt="" class="mb-3" />
            <Note />
          </div>
          <div class="col-6">
            <Size />
            <Table />
            <Usage />
          </div>
        </div>
        <div class="modal-footer d-flex justify-content-between">
          <button type="button" class="btn text-danger" @click="deleteCart">Delete</button>
          <button
            type="button"
            class="btn fw-bolder"
            @click="$emit('updata', tempCart.id, tempProduct.id)"
          >
            Update
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal';
import Note from '@/components/shop/NoteModel.vue';
import Size from './SizeCartModel.vue';
import Table from './TablewareCartModel.vue';
import Usage from './UsageCartModel.vue';

export default {
  data() {
    return {
      isLoading: false,
      editModal: '',
      tempCart: {},
      tempProduct: {},
      data: {},
    };
  },
  props: ['tempData'],
  watch: {
    tempData() {
      this.tempCart = { ...this.tempData };
      this.tempProduct = { ...this.tempData.product };
    },
  },
  components: {
    Size,
    Table,
    Usage,
    Note,
  },
  methods: {
    deleteCart() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${this.tempCart.id}`;
      this.$http.delete(api).then((res) => {
        console.log(res);
      });
    },
    toggleModal() {
      this.editModal.toggle();
    },
  },
  mounted() {
    this.editModal = new Modal(this.$refs.editModal);
  },
};
</script>
